// src/pages/AromaTreatment.js
import React from "react";
import "./MenuDetail.css";
import { useNavigate } from "react-router-dom"; // ✅ 追加！
import TitleLogo from "../assets/TitleLOGO.png"; // アロマトリートメントの画像をインポート

function AromaTreatment() {
  const navigate = useNavigate(); // ✅ 追加！

  const handleReserve = () => {
    navigate("/reservation", { state: { menu: "アロマトリートメント" } }); // ✅ 予約ページへ遷移
  };
  return (
    <div className="menu-detail">
      <h2>アロマトリートメント</h2>
      <img src={TitleLogo} alt="アロマトリートメント" className="menu-image" />

      <div className="menu-info">
        <h3>コース時間・料金</h3>
        <ul>
          <li>60分：6,000円</li>
          <li>90分：8,500円</li>
          <li>120分：11,000円</li>
        </ul>

        <h3>特徴</h3>
        <p>
          選べるアロマオイルで、心身をリラックス。リンパを流し、むくみや疲労感を解消します。
        </p>
      </div>

      <button className="reserve-button" onClick={handleReserve}>
        予約する
      </button>
    </div>
  );
}

export default AromaTreatment;
