// src/pages/HeadSpa.js
import React from 'react';
import './MenuDetail.css';
import { useNavigate } from 'react-router-dom'; // ✅ 追加！


function HeadSpa() {
    const navigate = useNavigate(); // ✅ 追加！

    const handleReserve = () => {
        navigate('/reservation', { state: { menu: 'ヘッドスパ' } }); // 🔥状態を渡す};// ✅ 予約ページへ遷移
    };

    return (
        <div className="menu-detail">
        <h2>ヘッドスパ</h2>
        <img src="/images/headspa.jpg" alt="ヘッドスパ" className="menu-image" />

        <div className="menu-info">
            <h3>コース時間・料金</h3>
            <ul>
            <li>30分：3,000円</li>
            <li>60分：5,500円</li>
            <li>90分：8,000円</li>
            </ul>

            <h3>特徴</h3>
            <p>
            頭皮のコリをほぐし、血行を促進します。心地よいマッサージで極上のリラックス体験を。
            </p>
        </div>

        <button className="reserve-button" onClick={handleReserve}>
            予約する
        </button>
        </div>
    );
}

export default HeadSpa;
