// src/pages/BodyCare.js
import React from 'react';
import './MenuDetail.css';
import { useNavigate } from 'react-router-dom'; // ✅ 追加！

function BodyCare() {
    const navigate = useNavigate(); // ✅ 追加！
    // 予約ボタンがクリックされたときの処理
    const handleReserve = () => {
        navigate('/reservation', { state: { menu: 'ボディケア' } }); // ✅ 予約ページへ遷移
    };
    return (
        <div className="menu-detail">
            <h2>ボディケア</h2>
            <img src="/images/bodycare.jpg" alt="ボディケア" className="menu-image" />

            <div className="menu-info">
                <h3>コース時間・料金</h3>
                <ul>
                <li>40分：4,000円</li>
                <li>60分：5,500円</li>
                <li>90分：7,500円</li>
                </ul>

                <h3>特徴</h3>
                <p>
                全身の筋肉をしっかりとほぐし、コリや疲労を解消。姿勢改善や代謝アップにも効果的です。
                </p>
            </div>

            <button className="reserve-button" onClick={handleReserve}>
                    予約する
            </button>
        </div>
    );
}

export default BodyCare;
