import React from "react";
import Header from "../../components/Header";
import MenuCard from "../../components/MenuCard";
import { Link } from "react-router-dom";
import UserNews from "../../components/UserNews";
import logo from "../../assets/INTIMACY_logo.png"; // ロゴ画像のパスを適宜変更
import menuLogo from "../../assets/TitleLOGO.png"; // メニュー画像のパスを適宜変更

const menuItems = [
  {
    title: "ヘッドスパ",
    description: "頭皮を優しくマッサージし、リラックスと血行促進を促します。",
    image: { menuLogo },
    path: "/menu/headspa",
  },
  {
    title: "ボディケア",
    description:
      "全身をほぐしてコリを解消し、深いリラクゼーションを提供します。",
    image: "../../assets/TitleLOGO.png",
    path: "/menu/bodycare",
  },
  {
    title: "アロマトリートメント",
    description: "厳選されたアロマオイルで心身ともに癒される極上の体験を。",
    image: "/images/aroma.jpg",
    path: "/menu/aroma",
  },
];

const Home = () => {
  return (
    <>
      <Header />
      <main>
        <div className="relative w-full h-96 overflow-hidden bg-black">
          {
            /* <video
            autoPlay
            muted
            loop
            playsInline
            className="absolute top-0 left-0 w-full h-full object-cover z-0">
            <source src="/videos/sample.mp4" type="video/mp4" />
            Your browser does not support the video tag.
          </video> */
            <img
              src={logo}
              className="absolute top-0 left-0 w-full h-full object-cover z-0"
              alt="hero-image"
            ></img>
          }
          <div className="relative z-10 flex items-center justify-center h-full text-white">
            <h1 className="text-4xl md:text-6xl font-bold">
              極上の癒しを身近に
            </h1>
          </div>
        </div>
      </main>
      <section className="py-16 bg-gray-100">
        <div className="p-4">
          <h2 className="text-3xl font-bold text-center mb-8">お知らせ</h2>
          <UserNews />
        </div>
        <div className="container mx-auto px-4">
          <h2 className="text-3xl font-bold text-center mb-8">メニュー</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {menuItems.map((item) => (
              <Link to={item.path} key={item.title} className="block">
                <MenuCard
                  title={item.title}
                  description={item.description}
                  image={item.image}
                />
              </Link>
            ))}
          </div>
        </div>
      </section>
      <Link
        to="/admin/reservations"
        className="inline-block mt-6 bg-green-600 text-white px-4 py-2 rounded"
      >
        予約一覧を見る
      </Link>
    </>
  );
};
export default Home;
